import React, { useState } from "react";
import {
  Container,
  Typography,
  TextField,
  Button,
  Grid,
  Card,
  CardContent,
  CircularProgress,
  Alert,
  Box,
  Divider,
  List,
  ListItem,
  ListItemText,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Checkbox,
  FormControlLabel,
  TableSortLabel,
} from "@mui/material";
import {
  Movie as MovieIcon,
  Description as DescriptionIcon,
  Person as PersonIcon,
  Mic as MicIcon,
  Update as UpdateIcon,
  Link as LinkIcon,
  Visibility as VisibilityIcon,
} from "@mui/icons-material";
import axios from "axios";

interface DashboardData {
  videoCount: number;
  transcriptCount: number;
  patientStoriesCount: number;
  kolInterviewsCount: number;
  lastUpdated: string;
}

interface VideoAnalysis {
  video: {
    video_id: string;
    search_name: string;
    title: string;
    description: string;
    published_date: string | null;
    duration_seconds: number;
    view_count: number;
    url: string;
    channel_name: string;
  };
  transcriptAvailable: boolean;
  analysis: {
    video_type: string;
    name: string | null;
    current_age: string | null;
    onset_age: string | null;
    sex: string | null;
    location: string | null;
    symptoms: any;
    medical_history_of_patient: any;
    family_medical_history: any;
    challenges_faced_during_diagnosis: any;
    key_opinion: string | null;
    topic_of_information: string | null;
    details_of_information: string | null;
    headline: string | null;
    summary_of_news: string | null;
  } | null;
}

interface PatientStory {
  video_id: string;
  title: string;
  description: string;
  url: string;
  published_date: string | null;
  view_count: number; // Added view_count
}

interface ApiResponse {
  type: "dashboardData" | "videoAnalysis" | "patientStories";
  data: DashboardData | VideoAnalysis | PatientStory[];
}

const Dashboard: React.FC = () => {
  const [searchName, setSearchName] = useState("");
  const [searchPhrase, setSearchPhrase] = useState("");
  const [videoUrl, setVideoUrl] = useState("");
  const [viewPatientStories, setViewPatientStories] = useState(false);
  const [response, setResponse] = useState<ApiResponse | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [sortDirection, setSortDirection] = useState<"asc" | "desc">("desc");
  const [sortField, setSortField] = useState<"published_date" | "view_count">("published_date"); // Added for sorting by multiple fields
  const [expandedDescriptions, setExpandedDescriptions] = useState<{ [key: string]: boolean }>({}); // Track expanded descriptions

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setResponse(null);

    try {
      const res = await axios.post("/api/dashboard", {
        searchName,
        searchPhrase,
        videoUrl,
        viewPatientStories,
      });
      setResponse(res.data);
    } catch (err: any) {
      setError(err.response?.data?.error || "An error occurred while fetching data");
    } finally {
      setLoading(false);
    }
  };

  const renderDashboardData = (data: DashboardData) => (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={6} md={4}>
        <Card
          sx={{
            backgroundColor: "#f5faff",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
            borderRadius: "12px",
            transition: "transform 0.2s ease-in-out",
            "&:hover": {
              transform: "translateY(-5px)",
            },
          }}
        >
          <CardContent>
            <Box display="flex" alignItems="center" mb={2}>
              <MovieIcon sx={{ fontSize: 40, color: "#1976d2", mr: 2 }} />
              <Typography variant="h6" sx={{ color: "#1976d2", fontWeight: "medium" }}>
                Videos Available
              </Typography>
            </Box>
            <Typography variant="h4" sx={{ color: "#1976d2", fontWeight: "bold" }}>
              {data.videoCount}
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12} sm={6} md={4}>
        <Card
          sx={{
            backgroundColor: "#f5faff",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
            borderRadius: "12px",
            transition: "transform 0.2s ease-in-out",
            "&:hover": {
              transform: "translateY(-5px)",
            },
          }}
        >
          <CardContent>
            <Box display="flex" alignItems="center" mb={2}>
              <DescriptionIcon sx={{ fontSize: 40, color: "#1976d2", mr: 2 }} />
              <Typography variant="h6" sx={{ color: "#1976d2", fontWeight: "medium" }}>
                Transcripts Available
              </Typography>
            </Box>
            <Typography variant="h4" sx={{ color: "#1976d2", fontWeight: "bold" }}>
              {data.transcriptCount}
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12} sm={6} md={4}>
        <Card
          sx={{
            backgroundColor: "#f5faff",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
            borderRadius: "12px",
            transition: "transform 0.2s ease-in-out",
            "&:hover": {
              transform: "translateY(-5px)",
            },
          }}
        >
          <CardContent>
            <Box display="flex" alignItems="center" mb={2}>
              <PersonIcon sx={{ fontSize: 40, color: "#1976d2", mr: 2 }} />
              <Typography variant="h6" sx={{ color: "#1976d2", fontWeight: "medium" }}>
                Patient Stories
              </Typography>
            </Box>
            <Typography variant="h4" sx={{ color: "#1976d2", fontWeight: "bold" }}>
              {data.patientStoriesCount}
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12} sm={6}>
        <Card
          sx={{
            backgroundColor: "#f5faff",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
            borderRadius: "12px",
            transition: "transform 0.2s ease-in-out",
            "&:hover": {
              transform: "translateY(-5px)",
            },
          }}
        >
          <CardContent>
            <Box display="flex" alignItems="center" mb={2}>
              <MicIcon sx={{ fontSize: 40, color: "#1976d2", mr: 2 }} />
              <Typography variant="h6" sx={{ color: "#1976d2", fontWeight: "medium" }}>
                KOL Interviews
              </Typography>
            </Box>
            <Typography variant="h4" sx={{ color: "#1976d2", fontWeight: "bold" }}>
              {data.kolInterviewsCount}
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12} sm={6}>
        <Card
          sx={{
            backgroundColor: "#f5faff",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
            borderRadius: "12px",
            transition: "transform 0.2s ease-in-out",
            "&:hover": {
              transform: "translateY(-5px)",
            },
          }}
        >
          <CardContent>
            <Box display="flex" alignItems="center" mb={2}>
              <UpdateIcon sx={{ fontSize: 40, color: "#1976d2", mr: 2 }} />
              <Typography variant="h6" sx={{ color: "#1976d2", fontWeight: "medium" }}>
                Last Updated
              </Typography>
            </Box>
            <Typography variant="h5" sx={{ color: "#1976d2", fontWeight: "medium" }}>
              {new Date(data.lastUpdated).toLocaleString()}
            </Typography>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );

  const renderVideoAnalysis = (data: VideoAnalysis) => (
    <Box>
      <Typography variant="h5" gutterBottom sx={{ color: "#1976d2", fontWeight: "bold" }}>
        Video Analysis
      </Typography>
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            Video Details
          </Typography>
          <List>
            <ListItem>
              <ListItemText primary="Title" secondary={data.video.title} />
            </ListItem>
            <ListItem>
              <ListItemText primary="Channel" secondary={data.video.channel_name} />
            </ListItem>
            <ListItem>
              <ListItemText
                primary="Published Date"
                secondary={data.video.published_date ? new Date(data.video.published_date).toLocaleString() : "N/A"}
              />
            </ListItem>
            <ListItem>
              <ListItemText
                primary="Duration"
                secondary={`${Math.floor(data.video.duration_seconds / 60)}m ${data.video.duration_seconds % 60}s`}
              />
            </ListItem>
            <ListItem>
              <ListItemText
                primary="View Count"
                secondary={data.video.view_count?.toLocaleString() || "N/A"}
              />
            </ListItem>
            <ListItem>
              <ListItemText
                primary="URL"
                secondary={
                  <a href={data.video.url} target="_blank" rel="noopener noreferrer">
                    {data.video.url}
                  </a>
                }
              />
            </ListItem>
            <ListItem>
              <ListItemText primary="Description" secondary={data.video.description || "N/A"} />
            </ListItem>
          </List>
        </CardContent>
      </Card>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            Transcript Status
          </Typography>
          <Typography variant="body2" color={data.transcriptAvailable ? "textPrimary" : "error"}>
            {data.transcriptAvailable ? "Transcript is Available" : "Transcript Not Available"}
          </Typography>
        </CardContent>
      </Card>

      {data.analysis ? (
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              Analysis
            </Typography>
            <List>
              <ListItem>
                <ListItemText primary="Video Type" secondary={data.analysis.video_type || "N/A"} />
              </ListItem>
              {data.analysis.video_type?.toLowerCase() === "patient story" && (
                <>
                  <ListItem>
                    <ListItemText primary="Name" secondary={data.analysis.name || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Current Age" secondary={data.analysis.current_age || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Onset Age" secondary={data.analysis.onset_age || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Sex" secondary={data.analysis.sex || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Location" secondary={data.analysis.location || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText
                      primary="Symptoms"
                      secondary={data.analysis.symptoms ? JSON.stringify(data.analysis.symptoms, null, 2) : "N/A"}
                    />
                  </ListItem>
                  <ListItem>
                    <ListItemText
                      primary="Medical History"
                      secondary={
                        data.analysis.medical_history_of_patient
                          ? JSON.stringify(data.analysis.medical_history_of_patient, null, 2)
                          : "N/A"
                      }
                    />
                  </ListItem>
                  <ListItem>
                    <ListItemText
                      primary="Family Medical History"
                      secondary={
                        data.analysis.family_medical_history
                          ? JSON.stringify(data.analysis.family_medical_history, null, 2)
                          : "N/A"
                      }
                    />
                  </ListItem>
                  <ListItem>
                    <ListItemText
                      primary="Challenges Faced During Diagnosis"
                      secondary={
                        data.analysis.challenges_faced_during_diagnosis
                          ? JSON.stringify(data.analysis.challenges_faced_during_diagnosis, null, 2)
                          : "N/A"
                      }
                    />
                  </ListItem>
                </>
              )}
              {data.analysis.video_type?.toLowerCase() === "kol interview" && (
                <ListItem>
                  <ListItemText primary="Key Opinion" secondary={data.analysis.key_opinion || "N/A"} />
                </ListItem>
              )}
              {["informational", "Informational"].includes(data.analysis.video_type || "") && (
                <>
                  <ListItem>
                    <ListItemText
                      primary="Topic of Information"
                      secondary={data.analysis.topic_of_information || "N/A"}
                    />
                  </ListItem>
                  <ListItem>
                    <ListItemText
                      primary="Details of Information"
                      secondary={data.analysis.details_of_information || "N/A"}
                    />
                  </ListItem>
                </>
              )}
              {data.analysis.video_type === "News Bulletin" && (
                <>
                  <ListItem>
                    <ListItemText primary="Headline" secondary={data.analysis.headline || "N/A"} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Summary of News" secondary={data.analysis.summary_of_news || "N/A"} />
                  </ListItem>
                </>
              )}
            </List>
          </CardContent>
        </Card>
      ) : (
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              Analysis
            </Typography>
            <Typography variant="body2" color="error">
              Analysis Not Available
            </Typography>
          </CardContent>
        </Card>
      )}
    </Box>
  );

  const renderPatientStories = (data: PatientStory[]) => {
    // Sort the data based on the selected field and direction
    const sortedData = [...data].sort((a, b) => {
      if (sortField === "published_date") {
        const dateA = a.published_date ? new Date(a.published_date).getTime() : 0;
        const dateB = b.published_date ? new Date(b.published_date).getTime() : 0;
        return sortDirection === "asc" ? dateA - dateB : dateB - dateA;
      } else {
        // Sort by view_count
        const viewCountA = a.view_count ?? 0;
        const viewCountB = b.view_count ?? 0;
        return sortDirection === "asc" ? viewCountA - viewCountB : viewCountB - viewCountA;
      }
    });

    const handleSort = (field: "published_date" | "view_count") => {
      if (sortField === field) {
        // If clicking the same field, toggle the direction
        setSortDirection(sortDirection === "asc" ? "desc" : "asc");
      } else {
        // If clicking a different field, set the new field and default to ascending
        setSortField(field);
        setSortDirection("asc");
      }
    };

    const toggleDescription = (videoId: string) => {
      setExpandedDescriptions((prev) => ({
        ...prev,
        [videoId]: !prev[videoId],
      }));
    };

    return (
      <Box>
        <Typography variant="h5" gutterBottom sx={{ color: "#1976d2", fontWeight: "bold" }}>
          Patient Stories
        </Typography>
        {sortedData.length === 0 ? (
          <Typography variant="body1" color="textSecondary">
            No patient stories found for this search term.
          </Typography>
        ) : (
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell sx={{ fontWeight: "bold" }}>Video ID</TableCell>
                  <TableCell sx={{ fontWeight: "bold" }}>Title</TableCell>
                  <TableCell sx={{ fontWeight: "bold", width: "200px" }}>Description</TableCell>
                  <TableCell sx={{ fontWeight: "bold" }}>URL</TableCell>
                  <TableCell sx={{ fontWeight: "bold" }}>
                    <TableSortLabel
                      active={sortField === "published_date"}
                      direction={sortField === "published_date" ? sortDirection : "asc"}
                      onClick={() => handleSort("published_date")}
                    >
                      Published Date
                    </TableSortLabel>
                  </TableCell>
                  <TableCell sx={{ fontWeight: "bold" }}>
                    <TableSortLabel
                      active={sortField === "view_count"}
                      direction={sortField === "view_count" ? sortDirection : "asc"}
                      onClick={() => handleSort("view_count")}
                    >
                      View Count
                    </TableSortLabel>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {sortedData.map((story) => {
                  const isExpanded = expandedDescriptions[story.video_id] || false;
                  const description = story.description || "N/A";
                  const truncatedDescription =
                    description.length > 100 ? description.substring(0, 100) + "..." : description;

                  return (
                    <TableRow key={story.video_id}>
                      <TableCell>{story.video_id}</TableCell>
                      <TableCell>{story.title}</TableCell>
                      <TableCell sx={{ width: "200px" }}>
                        {isExpanded ? (
                          <>
                            {description}
                            {description.length > 100 && (
                              <Typography
                                component="span"
                                sx={{ color: "#1976d2", cursor: "pointer", ml: 1 }}
                                onClick={() => toggleDescription(story.video_id)}
                              >
                                Show Less
                              </Typography>
                            )}
                          </>
                        ) : (
                          <>
                            {truncatedDescription}
                            {description.length > 100 && (
                              <Typography
                                component="span"
                                sx={{ color: "#1976d2", cursor: "pointer", ml: 1 }}
                                onClick={() => toggleDescription(story.video_id)}
                              >
                                ...
                              </Typography>
                            )}
                          </>
                        )}
                      </TableCell>
                      <TableCell>
                        <a href={story.url} target="_blank" rel="noopener noreferrer">
                          {story.url}
                        </a>
                      </TableCell>
                      <TableCell>
                        {story.published_date ? new Date(story.published_date).toLocaleString() : "N/A"}
                      </TableCell>
                      <TableCell>{story.view_count?.toLocaleString() || "N/A"}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Box>
    );
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ color: "#1976d2", fontWeight: "bold" }}>
        Video Analysis Dashboard
      </Typography>

      <Typography variant="body1" sx={{ mb: 2 }}>
        Enter a search term (e.g., "friedreich ataxia") to view aggregated metrics or patient stories, or provide a YouTube URL (e.g., "https://www.youtube.com/watch?v=ChN9qZTvKkw") to view the analysis of a specific video.
      </Typography>

      <Box component="form" onSubmit={handleSubmit} sx={{ mb: 4 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              label="Search Name"
              value={searchName}
              onChange={(e) => setSearchName(e.target.value)}
              variant="outlined"
              sx={{ backgroundColor: "#fff" }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Search Phrase (optional)"
              value={searchPhrase}
              onChange={(e) => setSearchPhrase(e.target.value)}
              variant="outlined"
              sx={{ backgroundColor: "#fff" }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="YouTube URL (optional)"
              value={videoUrl}
              onChange={(e) => setVideoUrl(e.target.value)}
              variant="outlined"
              sx={{ backgroundColor: "#fff" }}
            />
          </Grid>
          <Grid item xs={12} sm={1}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={viewPatientStories}
                  onChange={(e) => setViewPatientStories(e.target.checked)}
                  disabled={!!videoUrl}
                />
              }
              label="View Patient Stories"
            />
          </Grid>
          <Grid item xs={12} sm={1}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              fullWidth
              disabled={loading}
              sx={{ height: "56px" }}
            >
              {loading ? <CircularProgress size={24} /> : "Search"}
            </Button>
          </Grid>
        </Grid>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 4 }}>
          {error}
        </Alert>
      )}

      {response && response.type === "dashboardData" && renderDashboardData(response.data as DashboardData)}
      {response && response.type === "videoAnalysis" && renderVideoAnalysis(response.data as VideoAnalysis)}
      {response && response.type === "patientStories" && renderPatientStories(response.data as PatientStory[])}
    </Container>
  );
};

export default Dashboard;